import React, {useEffect, useState} from 'react';
import './App.css';
import {Layout, Menu, theme} from 'antd';
import BladeTable from "./components/bladeTable"
import RubberTable from "./components/rubberTable";
import PlayerTable from "./components/playerTable";

const {Header, Content, Footer} = Layout;

const items = [
    {key: 'blade', label: ''},
    {key: 'rubber', label: ''},
    {key: 'player', label: ''}
];

function App() {
    useEffect(() => {
        document.title = "";
    }, []);
    const [selectedKey, setSelectedKey] = useState('blade');
    const {
        token: {colorBgContainer, borderRadiusLG},
    } = theme.useToken();

    const handleMenuClick = (e: { key: React.SetStateAction<string>; }) => {
        setSelectedKey(e.key);
    };

    const renderContent = () => {
        switch (selectedKey) {
            case 'blade':
                return <BladeTable/>;
            case 'rubber':
                return <RubberTable/>;
            case 'player':
                return <PlayerTable/>;
            default:
                return null;
        }
    };

    return (
        <Layout>
            <Header style={{display: 'flex', alignItems: 'center'}}>
                <div className="demo-logo"/>
                <Menu
                    theme="dark"
                    mode="horizontal"
                    defaultSelectedKeys={['blade']}
                    items={items}
                    onClick={handleMenuClick} // 
                    style={{flex: 1, minWidth: 0}}
                />
            </Header>
            <Content style={{padding: '0 48px'}}>
                <div
                    style={{
                        background: colorBgContainer,
                        minHeight: 280,
                        padding: 24,
                        borderRadius: borderRadiusLG,
                    }}
                >
                    {renderContent()} {/*  selectedKey  */}
                </div>
            </Content>
            <Footer style={{textAlign: 'center'}}>
                 {new Date().getFullYear()} Created by skyskyskyha
            </Footer>
        </Layout>
    );
}

export default App;
