bladeData = bladeData.map((item, index) => ({ ...item, key: `${item.name}-${index}` }));

export const filterData = (searchText) => {
    const lowercasedText = searchText.toLowerCase();
    return bladeData.filter((item) =>
        Object.values(item).some((value) =>
            value.toString().toLowerCase().includes(lowercasedText)
        )
    );
};
